.signupForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    width: 300px;
    height: 70%;
    justify-content: space-evenly;
    transition: all 0.5s;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}

.signupForm > p {
    text-align: center;
    font: 400 1.8vmax "Roboto";
    text-decoration: underline;
    margin-bottom: 2rem;
    color: rgba(0, 0, 0, 0.687);
  }
.signupForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.signupForm>div>input,
.signupForm>div>select {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;

}


.signupForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.687);
}

.loginForm>a {
    color: rgba(0, 0, 0, 0.687);
    text-decoration: none;
    align-self: flex-end;
    transition: all 0.5s;
    font: 500 1vmax "Gill Sans";
}

.loginForm>a:hover {
    color: #000;
}

#registerImage > img{
width: 3vmax;
border-radius:100% ;
padding: 0.5vmax;
background-color: rgba(204, 204, 204, 0.532);
margin-right: 0.5vmax;
}

#registerImage > input{
    display: flex;
    padding: 0;

}

#registerImage > input::file-selector-button{
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.623);
    background-color: #fff;

}

#registerImage > input::file-selector-button:hover{
    background-color: rgb(230, 225, 225);
}
.signupBtn {
    border: none;
    background-color: tomato;
    color: #fff;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}


.signupBtn:hover {
    background-color: rgb(179, 66, 46);

}
